<script>
  import ContentLoader from "svelte-content-loader";

  export let count = 3;

  function randomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
  }
</script>

<div class="loader-wrapper">
{#each Array(count) as _}
  <ContentLoader height="110" viewBox="0 0 250 110" primaryColor="#dddddd" secondaryOpacity="0">
    <rect x="0" y="0" rx="3" ry="3" width={randomInt(80, 220)} height="10" />
    <rect x="0" y="35" rx="3" ry="3" width={randomInt(80, 220)} height="10" />
    <rect x="0" y="70" rx="3" ry="3" width={randomInt(80, 220)} height="10" />
  </ContentLoader>
{/each}
</div>

<style lang="scss">
  .loader-wrapper {
    margin-top: 1rem;
    padding-left: 2rem;
    display: flex;
    flex-direction: column;
  }
</style>
