<script>
  import { onMount } from "svelte";
  import Split from "split.js";
  import NavLoader from "./Nav-loader.svelte";
  import DocsLoader from "./Docs-loader.svelte";
  import { content, contentDate, contentPrefetched } from "./stores/content";
  import { fly } from "svelte/transition";

  $: meta = {};
  let initializing = true;
  let fetching = false;
  let debounceTimer;
  let inputVal = "";
  let searchVal = "";
  let searchInput;
  let searchInProgress = false;
  let selectedNav = "";
  let menuVisible = true;
  const screen = window.matchMedia("(max-width: 799px)");
  $: isMobile = screen.matches;
  let htmlSource;
  let navSource = [];
  let docsSource = [];
  let splitInstance = {};
  let notifyUpdate = false;
  $: docs = docsSource
    .filter((doc) => {
      const val = searchVal.toLowerCase();
      let filteredDoc = doc.id?.includes(val) || doc.content?.includes(val);
      return filteredDoc;
    })
    .map((doc) => (doc = { ...doc, content: searchVal ? highlightText(doc.content) : doc.content }));

  let isFocused = false;
  const onInputFocus = () => (isFocused = true);
  const onInputBlur = () => (isFocused = false);
  let highlightedDoc = null;

  screen.addEventListener("change", (e) => {
    if (e.matches) {
      if (menuVisible) menuVisible = false;
      isMobile = true;
      destroySplit();
    } else {
      isMobile = false;
      menuVisible = true;
      setTimeout(() => {
        initSplit();
      });
    }
  });

  const htmlUrl = "https://cors.bridged.cc/https://www.tvpaint.com/doc/tvpaint-animation-11/george-commands";

  // const testUrl = fetch("https://www.tvpaint.com/doc/tvpaint-animation-11/george-commands", {
  //   method: "GET",
  //   headers: {},
  //   credentials: "same-origin",
  // })
  //   .then(function (response) {
  //     response.status;
  //     response.statusText;
  //     response.headers;
  //     response.url;

  //     return response.text();
  //   })
  //   .catch(function (error) {
  //     error.message;
  //   });

  // const url =
  //   "https://raw.githubusercontent.com/AsafAgranat/asafagranat.github.io/master/files/TVP_george_ref.txt";

  onMount(async () => {
    // await fetch("https://www.tvpaint.com/doc/tvpaint-animation-11/george-commands", {
    //   method: "GET",
    //   headers: {},
    //   credentials: 'same-origin'
    // }).then(res => console.log(res))

    if ($content) {
      processFetchResponse($content, true);
      initSplit();
    } else {
      await fetch(htmlUrl)
        .then((res) => {
          return res.text();
        })
        .then((str) => {
          $content = str;
          processFetchResponse(str);
          initSplit();
        });
    }
  });

  function highlightText(string) {
    let textArr = [];
    const [...arr] = [...htmlFromString(string)];
    const regex = new RegExp(searchVal, "ig");
    for (let i = 0; i < arr.length; i++) {
      const el = arr[i];
      if (el.nodeName === "#text") {
        const subst = '<span class="highlighted">$&</span>';
        const result = el.nodeValue.replaceAll(regex, subst);
        textArr.push(result);
      } else if (el.nodeName === "A") {
        const subst = `<span class="highlighted">$&</span>`;
        const replaced = el.innerText.replace(regex, subst);
        const result = `<a href="#${el.innerText.toLowerCase()}"><span class="george-function">${replaced}</span></a>"`;
        textArr.push(result);
      } else {
        textArr.push(el.outerHTML);
      }
    }
    return textArr.join("");
  }

  function processFetchResponse(str, checkContentUpdate = false) {
    const substr = '<p class="code">';
    const position = str.indexOf(substr);

    htmlSource = str
      .slice(position + substr.length)
      .split(/------|------/g)
      .filter((val) => val !== "");

    setNavSource(htmlFromString(htmlSource[0])).then((_navSource) => {
      setDocsSource(htmlSource.slice(1)).then((_docsSource) => {
        fetchContentFromRemote().then((_freshContent) => {
          if (checkContentUpdate) {
            const lastUpdate = getMeta(extractedFooter(_freshContent)).updated;
            $contentPrefetched = _freshContent;
            notifyUpdate = lastUpdate !== $contentDate;
          }
          fetching = false;
        });
      });
    });
  }

  function extractedFooter(str) {
    const fromSubst = "<footer>",
      toSubst = "</footer>";
    const len = fromSubst.length;
    const substring = str.substring(str.lastIndexOf(fromSubst) + len, str.lastIndexOf(toSubst));
    return [...htmlFromString(substring)];
  }

  async function fetchContentFromRemote() {
    return fetch(htmlUrl).then((res) => res.text());
  }

  async function setDocsSource(docsNodes) {
    return await new Promise((resolve) => {
      docsNodes.forEach((entry, i) => {
        const node = htmlFromString(entry);

        function nodesToString(nodelist) {
          let [...nodes] = nodelist;
          let arr = nodes.slice(nodes[0].nodeName === "#text" ? 2 : 1);
          let text = nodelistFromArray(arr);
          return text
            .replace("[PARAMETERS]", "<h3>Parameters</h3>")
            .replace("[ERROR]", "<h3>Error</h3>")
            .replace("[WARNING]", "<h3>Warning</h3>")
            .replaceAll("[RETURN]", "<h3>Return</h3>")
            .replaceAll("[COMMENT]", "<h3>Comment</h3>");
        }
        const docId = node[node[0].nodeName === "#text" ? 1 : 0].innerText;
        const navItem = navSource.find((doc) => doc?.index === i);
        let doc = {
          index: i,
          id: docId,
          ...(node[0].nodeName === "#text" && { version: node[0].textContent }),
          content: nodesToString(node),
          brief: navItem?.brief,
          multiple: navItem?.multiple ? navItem.multiple : null,
        };

        if (docsNodes.length === i + 1) {
          getFooter(entry)
            .then((footer) => {
              return getMeta(footer);
            })
            .then((res) => {
              meta = res;
              $contentDate = res.updated;
              initializing = false;
            });
        } else {
          docsSource = [...docsSource, doc];
        }
      });
      resolve("done docs");
    });
  }

  async function setNavSource(navNodes) {
    return await new Promise((resolve) => {
      navNodes.forEach((entry, i) => {
        function testMulti(id, index, text) {
          return !!(navSource.find((item) => item.id === id) || navNodes[i + 2]?.innerText === id);
        }

        if (entry.nodeName === "A") {
          let navItem = {
            index: i / 2,
            id: entry.innerText,
            brief: navNodes[i + 1].textContent,
            multiple: testMulti(entry.innerText, i, navNodes[i + 1].textContent),
          };
          navSource = [...navSource, navItem];
        }
      });
      resolve("done nav");
    });
  }

  async function getFooter(content) {
    return await new Promise((resolve) => {
      htmlFromString(content).forEach((node) => {
        if (node.nodeName === "FOOTER") {
          resolve([...node.childNodes]);
        }
      });
    });
  }

  function extractDate(nodesAsArray) {
    return nodesAsArray
      .find((el) => el.className === "last-update")
      .innerText.split(":")
      .pop()
      .trim();
  }

  function extractCopyright(nodesAsArray) {
    return nodesAsArray.find((el) => el.className === "copyright").innerText.trim();
  }

  function getMeta(nodesAsArray) {
    return {
      updated: extractDate(nodesAsArray),
      copyright: extractCopyright(nodesAsArray),
    };
  }

  function initSplit() {
    splitInstance = Split(["#nav", "#main"], {
      sizes: [25, 75],
      minSize: [100, 300],
    });
  }

  function destroySplit() {
    splitInstance.destroy();
  }

  function htmlFromString(htmlString) {
    var div = document.createElement("div");
    div.innerHTML = htmlString.trim();
    return div.childNodes;
  }

  function nodelistFromArray(arrayOfNodes) {
    var div = document.createElement("div");
    arrayOfNodes.forEach((item) => div.appendChild(item));
    return div.innerHTML;
  }

  function clearInput() {
    inputVal = "";
    searchVal = "";
    if (isMobile) searchVisible = false;
  }

  function toggleMenu() {
    menuVisible = !menuVisible;
  }

  function handleKeydown(event) {
    if (isMobile) searchVisible = true;
    setTimeout(() => {
      if (document.activeElement.id !== "searchInput" && event.keyCode >= 65 && event.keyCode <= 90) {
        inputVal = event.key;
        searchInput.focus();
        handleInput();
      }
    });
  }

  function handleMainClick() {
    if (!isMobile) return;
    if (menuVisible) {
      toggleMenu();
    }
  }

  function handleNavClick(name) {
    if (!isMobile) return;
    selectedNav = name;
    toggleMenu();
  }

  function handleSearchButtonClick() {
    searchVisible = true;
  }

  function highlightDoc(id, index) {
    highlightedDoc = `${id}_${index}`;
    setTimeout(() => {
      highlightedDoc = null;
    }, 500);
  }

  function handleInput() {
    searchInProgress = true;
    clearTimeout(debounceTimer);
    debounceTimer = setTimeout(() => {
      searchVal = inputVal;
      searchInProgress = false;
    }, 600);
  }

  function onInputKeydown(ev) {
    if (ev.key === "ArrowDown") {
    }
  }

  function updateContent() {
    fetching = true;
    setTimeout(() => {
      $content = $contentPrefetched;
      processFetchResponse($contentPrefetched);
      notifyUpdate = false;
    });
  }
</script>

<svelte:head>
  <title>Blah</title>
</svelte:head>
<svelte:window on:keydown={handleKeydown} />
<header>
  <div>
    {#if isMobile}
      <button class="menu-button" on:click={toggleMenu}>☰</button>
    {/if}
    <h1 class:is-mobile={isMobile}><a href="#" class="header-logo"><img src="https://www.tvpaint.com/doc/tvp11/style/tvpaint.default/images/header-logo.png" alt="" /></a>George Script documentation</h1>
  </div>
  <div>
    {#if !initializing}
      {#if notifyUpdate}
        <button class="meta-date meta-notification" on:click={updateContent} in:fly={{ y: 30, duration: 300 }} out:fly={{ y: -30, duration: 300 }}>
          {#if !fetching}
            New content! click to refresh
          {:else}
            Fetching...
          {/if}
        </button>
      {:else}
        <div class="meta-date" in:fly={{ y: 30, duration: 300 }} out:fly={{ y: -30, duration: 300 }}>
          Updated: {meta.updated}
        </div>
      {/if}
    {/if}
  </div>
</header>
<div class="wrapper">
  {#if menuVisible}
    <nav id="nav" class:is-mobile={isMobile}>
      {#if searchInProgress}
        <div class="loading-blocker" />
      {/if}
      <div class="nav-inner">
        {#if initializing}
          <div class="nav-loader">
            <NavLoader count={5} />
          </div>
        {:else}
          <div class="search-input-wrapper" class:is-mobile={isMobile} on:keydown={onInputKeydown}>
            <input
              type="text"
              bind:value={inputVal}
              on:input={handleInput}
              bind:this={searchInput}
              class="search-input"
              class:is-mobile={isMobile}
              class:is-busy={inputVal.length}
              class:is-focused={isFocused}
              class:has-error={!initializing && docs.length === 0}
              on:focus={onInputFocus}
              on:blur={onInputBlur}
              id="searchInput"
              placeholder="Start typing"
            />
            <button class="clear-input" on:click={clearInput}>✕</button>
          </div>
        {/if}
        {#each docs as doc}
          <a href={`#${doc.id}_${doc.index}`} class:selected={selectedNav === doc.id} on:click={handleNavClick(doc.id)} title={`${doc.brief}`} on:click={highlightDoc(doc.id, doc.index)}>
            <span class="link-prefix">tv_</span>{doc.id.replace("tv_", "")}
            {#if doc.multiple}
              <span class="link-brief">{doc.brief.replace('["toolmode"]', "")}</span>
            {/if}
          </a>
        {/each}
      </div>
    </nav>
  {/if}

  <main id="main" class:is-blocked={isMobile && menuVisible} on:click={handleMainClick} class:is-loading={initializing}>
    <!-- <input bind:value={$content} /> -->
    <!-- {$content} -->
    {#if searchInProgress}
      <div class="loading-blocker" />
    {/if}
    {#if initializing}
      <div class="nav-loader">
        <DocsLoader count={5} />
      </div>
    {/if}
    {#if docs.length > 0}
      {#each docs as doc}
        <div class="anchor" id={doc.id}><span id={`${doc.id}_${doc.index}`} /></div>
        <article class:is-highlighted={highlightedDoc === `${doc.id}_${doc.index}`}>
          <div class="article-header">
            <h2>
              {doc.id}
            </h2>
            {#if doc.version}<span class="article-version">{doc.version}</span>{/if}
          </div>
          <div class="article-content">
            <section>
              <pre>{@html doc.content}</pre>
            </section>
          </div>
        </article>
      {/each}
    {:else if !initializing}
      <div class="no-content">No matching docs</div>
    {/if}
    <div class="meta-copyright">
      {meta.copyright}
    </div>
  </main>
</div>

<style lang="scss">
  @import "./styles/vars.scss";

  $inputBoxShadow: 0 0 15px 0 rgb(0 0 0 / 7%), 0 1px 1px 0 rgb(0 0 0 / 10%);
  $error-color: tomato;
  :root {
    --header-height: 68px;
    --input-border-color: #{$yellow};
  }
  header {
    position: fixed;
    height: var(--header-height);
    padding: 1rem;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    > div {
      display: flex;
      align-items: center;
    }
  }
  h1 {
    font-size: 1.4rem;
    margin: 0;
    display: flex;
    align-items: center;
    &.is-mobile {
      font-size: 1rem;
    }

    img {
      width: 120px;
    }

    a {
      display: flex;
      margin-right: 0.5rem;
    }
  }
  .menu-button {
    margin: 0;
    width: 40px;
    margin-right: 1rem;
  }
  .search-input {
    width: 400px;
    margin: 0;
    padding: 0.6em;
    border-radius: 2em;
    padding-left: 1rem;
    max-width: 100%;
    font-weight: 600;
    box-shadow: $inputBoxShadow;
    border: none;
    &.is-mobile {
      width: calc(100vw - 1rem - var(--header-height));
    }

    &.is-busy {
      box-shadow: $inputBoxShadow, inset 0 0 0 5px var(--input-border-color);
    }

    &.has-error {
      --input-border-color: #{$error-color};
    }
  }

  .search-input-wrapper {
    position: sticky;
    top: 0;
    align-self: flex-start;
    max-width: 100%;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-bottom: 0.5rem;
    z-index: 5;
    &.is-mobile {
      padding-right: 0.5rem;
    }
  }
  .clear-input {
    position: absolute;
    z-index: 100;
    width: 36px;
    height: 36px;
    margin: 0;
    border: none;
    background: transparent;
    right: calc(1rem + 2px);
    &:focus {
      outline: auto;
    }
  }
  .wrapper {
    display: flex;
    height: calc(100%);
    padding-top: var(--header-height);
  }

  .loading-blocker {
    position: absolute;
    background: var(--page-bg);
    opacity: 0.5;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 3;
  }
  nav {
    position: relative;
    direction: rtl;
    overflow: auto;
    overflow-x: hidden;
    min-width: 220px;
    height: 100%;
    padding: 0.25rem;
    padding-top: 0;

    .nav-inner {
      display: flex;
      flex-direction: column;
      direction: ltr;
    }

    a {
      padding: 0.5rem 1rem;
      text-decoration: none;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
      &:hover,
      &:focus {
        background-color: #eee;
      }
      &:active {
        background-color: #ddd;
      }
    }
    &.is-mobile {
      box-shadow: 40px 0px 35px -35px rgb(0 0 0 / 10%);
    }

    .link-prefix {
      opacity: 0.3;
    }

    .link-brief {
      font-weight: initial;
      color: initial;
      font-style: italic;
      opacity: 0.8;
      white-space: pre;
    }
  }
  main {
    position: relative;
    overflow-x: hidden;
    padding: 0 1rem 2rem 1rem;
    &.is-blocked {
      // pointer-events: none;
      user-select: none;
    }

    &.is-loading {
      overflow: hidden;
    }
  }
  .anchor {
    margin-top: 10px;
    margin-bottom: -10px;
    height: 10px;
  }
  article {
    padding: 1rem 2rem;
    margin-top: 1rem;
    box-shadow: 0 0 15px 0 rgb(0 0 0 / 5%);
    background: white;
    position: relative;

    > * {
      position: relative;
    }

    h3 {
      text-transform: uppercase;
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: lightyellow;
      opacity: 0;
      z-index: 0;
      will-change: opacity;
      transition: opacity 1.5s;
    }
    &.is-highlighted {
      &:before {
        opacity: 1;
        transition: opacity 0.15s;
      }

      .article-header {
        background: transparent;
        transition: background 0s;
      }
    }
  }
  .article-header {
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background: white;
    padding: 1rem 0;
    will-change: background;
    transition: background 0s 2s;
    z-index: 1;
  }
  .article-version {
    background-color: #eee;
    font-weight: 700;
    border-radius: 14px;
    padding: 0 0.5rem;
    margin-left: 1rem;
  }
  .article-content {
    overflow: auto;
  }

  @media screen and (max-width: 799px) {
    nav {
      position: fixed;
      left: 0;
      width: 280px !important;
      max-width: none !important;
      bottom: 0;
      height: calc(100% - 68px);
      background: var(--page-bg);
      z-index: 10;
    }
    main {
      width: 100% !important;
    }

    .menu-button {
      display: block;
    }
  }

  .meta-date {
    padding: 0.5rem 1rem;
    background: #eee;
    display: inline-block;
    border-radius: 1rem;
    font-weight: 600;
    position: absolute;
    right: 1rem;
  }

  .meta-notification {
    background-color: $yellow;
    margin: 0;
    appearance: none;
    border: none;
    cursor: pointer;
  }

  .meta-copyright {
    padding: 2rem;
    margin-top: 2rem;
  }

  .no-content {
    @extend article;
    display: inline-block;
  }
</style>
