import { writable } from 'svelte/store';

const stored = localStorage.content
const storedDate = localStorage.contentDdate
const prefetched = localStorage.contentPrefetched

export const content = writable(stored)
export const contentDate = writable(storedDate)
export const contentPrefetched = writable(prefetched || 'none')

content.subscribe(val => localStorage.content = val)
contentDate.subscribe(date => localStorage.contentDate = date)
contentPrefetched.subscribe(val => localStorage.contentPrefetched = val)
